
.container-xs {
  @extend .container;
  max-width: $screen-xs;
}

.container-sm {
  @extend .container;
  max-width: $container-sm;
}

.container-md {
  @extend .container;
  max-width: $container-md;
}

// Page header.
.page-header {
  border: none;
}

.spacer-h { height: 35px ; }

.path-examples {
  .main-content {
    margin: 15px ;
  }
}

.section-with-title {
  margin-top: 45px ;
  border-top: 1px solid $gray ;
  text-align: center;
  .section-title, .block-title {
    text-align: center;
    position: relative;
    top: -30px ;
    display: inline-block;
    margin: 15px auto ;
    background-color: white;
    padding: 0 25px ;
  }
}

.views-row {
  margin: $padding-large-vertical auto ;
}

.project-list {
  & > .view-header {
    text-align: left;
  }
  & > .view-content {
    @extend .container;
  }
}



