/**
 * Panel styling.
 */
.panel-title {
  display: block;
  margin: -10px -15px;
  padding: 10px 15px;
  &, &:hover, &:focus, &:hover:focus {
    color: inherit;
  }
  &:focus, &:hover {
    text-decoration: none;
  }
}

.panel {
  .bg-image {
    padding: $padding-base-vertical $padding-base-horizontal ;
  }

  .panel-heading {
    h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
      margin: $padding-small-vertical auto;
    }
  }

  .panel-infobar {
    margin-bottom: $padding-small-vertical;
  }

  .panel-footer {
    padding: $padding-base-horizontal ($padding-base-horizontal * 2) ($padding-base-horizontal * 3) ;
  }

  .media-left .fa {
    font-size: 21px ;
    width: 21px;
    text-align: center;
  }

  .inline-avatar {
    .img-responsive {
      display: inline-block;
      width: 40px;
      height: 40px ;
      vertical-align: text-bottom;
    }
  }

}

.panel-event {
  .panel-heading {
    background-color: $dark-red ;
    color: $navbar-inverse-link-color;
    font-weight: bold;
  }

  .panel-body {
    .field--type-text-with-summary {
      & , & > * {
        text-align: left !important;
      }
      h1, h1 > span {
        font-size: $font-size-h3 !important;
      }
    }
  }

}



